const initialState = {
  products: [],
  isloading:false,
  prizes:[],
  addprize:[],
  editpriz:[],
  editprizs:[],
  wishlist: [],
  cart: [],
  productDetail: {},
  params: {},
  prizesEdit: [],
  toggle_data: '',
  totalProducts: 0,
  check:[],
  del :false,
  prizefail:false,
  allPrizes:[]
}

const ecommerceReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { prizes:[] }
    case 'GET_ALL_PRIZE_INIT':
      return { ...state, isloading:true }
    case 'GET_PRIZES':
      return { ...state, prizes: [...state.prizes, action.data.data.data], isloading: false }
    case 'PRIZE_FAIL':
      return { prizefail: action.data }
    case 'PRIZE_DELETE':
    return { del: true }
    case 'DELETE_PRIZE':
      return { ...state }
    case 'EDIT_PRIZE':
      return { ...state, editpriz: action.data.data, isloading: false }
    case 'EDIT_PRIZES':
      return { ...state, editprizs: action.data.data }
    case 'GET_PRODUCTS':
      return {
        ...state,
        products: action.data.products,
        params: action.params,
        totalProducts: action.data.total,
        isloading: false
      }
    case 'GET_WISHLIST':
      return { ...state, wishlist: action.data.products }
    case 'DELETE_WISHLIST_ITEM':
      return { ...state }
    case 'GET_CART':
      return { ...state, cart: action.data.products }
    case 'DELETE_CART_ITEM':
      return { ...state }
    case 'ADD_TO_WISHLIST':
      return { ...state }
    case 'ADD_TO_CART':
      return { ...state }
    case 'PRIZES_EDIT':
      return { ...state, prizesEdit: action.data.data.records }
    case 'GET_PRODUCT':
      return { ...state, productDetail: action.data.product }
    case 'MANAGE_PRIZE_TOGGLE_STATUS':
      return {...state, check:action.data.data, toggle_data: action.data} 
    case 'GET_TOTAL_PRIZES':
      return { ...state, allPrizes: action.data.data }     
    default:
      return state
  }
}

export default ecommerceReducer