const initialState = {
  allCricketdata: [],
  data:[],
  quizdata:[],
  loaDataAll:false
  
}

const winner = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_ALL_CRICKET_CONTESTS_REPORT':
      return { ...state, allCricketdata: action.data.data, loaDataAll: false }
    case 'GET_ALL_WALL_DATA_FAIL':
        return { ...state, loadDataAll: false }  
    case 'GET_ALL_WALL_DATA_INIT':
        return { ...state, loadFeedAll: true }    
    case 'GET_ALL_WINNER_CONTESTS_REPORT':
      return { ...state, data: action.data.data }
      case 'GET_ALL_Quiz_CONTESTS_REPORT':
        return { ...state, quizdata: action.data.data }  
    default:
      return { ...state }
  }
}

export default winner
