const initialState = {
  allUsers: [],
  userDetails: [],
  getTypeList: [],
  loadAllUsers: false,
  user_per_page:[],
  transactions:[],
  fixtures:[],
  usersearch:[],
  contests:[],
  prize:[],
  referrals:[],
  searchUsers:[],
  allUsersPerPage:[]
}

const users = (state = initialState, action) => {
  switch (action.type) {
    case 'SETPAGE':
      return { ...state, user_per_page:[] }
    case 'CLEAR_DATA':
      return {...state, searchUsers: []}
    case 'GET_ALL_USERS_INIT':
      return { ...state, loadAllUsers: true }
    case 'FETCH_PAGE':
      return { ...state, page: action.data }
    case 'GET_ALL_USERS':
      return { ...state, allUsers: action.data.data, user_per_page: action.data, loadAllUsers: false }
    case 'GET_ALL_USERS_SEARCH':
      return { ...state, usersearch: action.data.data, user_per_page: action.data, loadAllUsers: false }
    case 'GET_ALL_USERS_FAIL':
      return { ...state, loadAllUsers: false }
    case 'GET_USER_DETAILS':
      return { ...state, userDetails: action.data }
    case 'GET_TYPE_LIST':
      return { ...state, getTypeList: action.data }
    case 'GET_TRANSACTION':
      return { ...state, transactions: action.data, loadAllUsers: false }
    case 'GET_MATCHES':
      return { ...state, fixtures: action.data, loadAllUsers: false }
    case 'GET_PRIZE':
      return { ...state, prize: action.data, loadAllUsers: false }
    case 'GET_CONTESTS':
      return { ...state, contests: action.data, loadAllUsers: false }
    case 'GET_REFERRRAL':
      return { ...state, referrals: action.data, loadAllUsers: false }
    case 'RESET_USER_PROFILE':
      return { ...state, userDetails: [] }
    case 'ADD_RANGE_USERS':
      return { ...state, rangeusers: state.allUsers }  
    case 'SEARCH_USERS':
      return { ...state, searchUsers: action.data, allUsers:[] }  
    case 'GET_ALL_USERS_PER_PAGE':
      return { ...state, allUsersPerPage: action.data }  
    default:
      return { ...state }
  }
}

export default users
