// ** Initial State
const initialState = {
  lang_count:null,
  eng_question:[],
  eng_answers:[],
  langs:[],
  quiz_det:{},
  allData: [],
  addloader: false,
  addedQuizID: 0,
  addedQuizID2: null,
  questions: [],
  data: [],
  total: 1,
  params: {},
  list:[],
  quizQuestionsData: [],
  quizAnswersData:[],
  isLoading: false,
  loadAllData:false,
  quizEditDetails: [],
  quizEditQuestions: [],
  quizEditAnswers: [],
  //players and quizes New
  players: [],
  quizes: [],
  quizDetailsData:[],
  quiz_page: [],
  cric_data:[],
  foot_data:[],
  bask_data:[],
  cric_quiz_page:[],
  foot_quiz_page:[],
  bask_quiz_page:[],
  next_page: false,
  toggle_status:""
}

const quiz = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_ALL_DATA_INIT':
      return { ...state, loadAllData:true }
    case 'LANGS_ID':
      return {...state, langs:action.data.data }
    // case 'QUIZ_DETAILS':
    //   return {...state, master_language_id:action.data, translation_quiz_name:action.data   }
    case 'LANG_ID':
      return {...state, lang_count:action.data }
    case 'QUIZ_ENG_QUESTION':
      return { ...state, eng_question:action.data  }
    case 'QUIZ_ENG_ANSWERS':
      return {...state, eng_answers:action.data  }
    case 'GET_ALL_DATA':
      return { ...state, data: action.data.data, quiz_page:action.data, loadAllData:false }
    case 'GET_CRIC_QUIZ':
      return { ...state, cric_data: action.data.data, cric_quiz_page:action.data, loadAllData:false }
    case 'GET_FOOT_QUIZ':
      return { ...state, foot_data: action.data.data, foot_quiz_page:action.data, loadAllData:false }      
    case 'GET_BASK_QUIZ':
      return { ...state, bask_data: action.data.data, bask_quiz_page:action.data, loadAllData:false }            
    case 'QUIZ_ADD':
      return { ...state, addedQuizID: action.data, next_page: action.data }
    // Number of players and number of quizes start
    case 'NUMBER_PLAYERS':
      return { ...state, players: action.data.data }
    case 'NUMBER_QUIZES':
      return { ...state, quizes: action.data.data }
    // Number of players and number of quizes End
    case 'ADD_QUIZ_POST_INIT': 
      return { ...state, addloader: true}
    case 'QUIZ_QUESTION_ADD':
      return { ...state, addedQuizID2: action.data.data, addloader:false }
    case 'RESET_QUESTIONS_ADDED':
      return { ...state, addedQuizID: null }
    case 'QUIZ_TOGGLE_STATUS':
      return { ...state, toggle_status: action.data }
    case 'DETAILS_VIEW_INIT':
        return { ...state, isLoading:true }
    case 'QUIZ_DET':
      return { ...state, quizDetailsData: action.data, isLoading:false }
    case 'QUIZ_QUESTIONS_DATA':
      return { ...state, quizQuestionsData: action.data, isLoading: false }
    case 'QUIZ_ANSWERS_DATA':
      return { ...state, quizAnswersData: action.data, isLoading: false }    
    case 'RESET_QUIZ_QUESTIONS':
      return { ...state, quizQuestionsData: [] }
    case 'QUIZ_EDIT':
      return { ...state, quizEditDetails: action.data }
    case 'QUIZ_EDIT_QUESTIONS':
      return { ...state, quizEditQuestions: action.data }
    case 'QUIZ_EDIT_ANSWERS':
      return { ...state, quizEditAnswers: action.data }
    default:
      return { ...state }
  }
}
export default quiz
