const initialState = {
  cricket_data: [],
  football_data: [],
  isloading: false,
  cricket_prize_page: [],
  football_prize_page: [],
  cricket_series_name: [],
  cricket_fixture_name: [],
  football_series_name: [],
  football_fixture_name: [],
  seriesid: "",
  fixtureid: '',
  cricket_add_data: [],
  football_add_data: [],
  view_cricket_contest_prize: [],
  cricket_total: [],
  football_total: []
}

const contestsprizes = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_add_data: [], football_add_data: [] }
    case 'GET_ALL_CRICKET_CONTEST_PRIZE':
      return { ...state, isloading: true }
    case 'GET_ALL_USERS_FAIL':
      return { ...state, isloading: false }
    case 'CRICKET_CONTESTS_PRIZR':
      return { ...state, cricket_data: action.data.data, cricket_prize_page: action.data, isloading: false }
    case 'CRICKET_CONTEST_PRIZE_ADD':
      return { ...state, cricket_add_data: action.data.data, cricket_prize_page: action.data, isloading: false }
    case 'VIEW_RICKET_CONTESTS_PRIZE':
      return { ...state, view_cricket_contest_prize: action.data.data }
    case 'FOOTBALL_CONTESTS_PRIZR':
      return { ...state, football_data: action.data.data, football_prize_page: action.data }
    case 'CRICKET_SERIES_NAME':
      return { ...state, cricket_series_name: action.data.data, isloading: false }
    case 'CRICKET_FIXTURE_NAME':
      return { ...state, cricket_fixture_name: action.data.data, isloading: false }
    case 'CRICKET_COLLECT_SERIESID':
      return { ...state, seriesid: action.data }
    case 'CRICKET_COLLECT_FIXTUREID':
      return { ...state, fixtureid: action.data }
    case 'FOOTBALL_SERIES_NAME':
      return { ...state, football_series_name: action.data.data, isloading: false }
    case 'FOOTBALL_FIXTURE_NAME':
      return { ...state, football_fixture_name: action.data.data, isloading: false }
    case 'FOOTBALL_CONTEST_PRIZE_ADD':
      return { ...state, football_add_data: action.data.data, football_prize_page: action.data }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    case 'FOOTBALL_SERIES_TOTAL':
      return { ...state, football_total: action.data }
    default:
      return { ...state }
  }
}

export default contestsprizes
