// ** Initial State
const initialState = {
  isloading: false,
  download_timer : false,
  fixloader:true,
  football_contest: [],
  football_winner : [],
  football_quiz : [],
  contest_fot_from:'',
  contest_fot_to:'',
  winner_fot_from:'',
  winner_fot_to:'',
  quiz_fot_from:'',
  quiz_fot_to:'',
  football_contest_perpage: [],
  football_winner_perpage: [],
  football_quiz_perpage:[],
  football_series_name:[],
  football_fixture_name:[],
  football_player_reports:[],
  football_seriesid:'',
  football_fixtureid:'',
  getval:"",
  date_range: 91
}

const reports = (state = initialState, action) => {
  
    switch (action.type) {
      case 'CLEAR_DATA':
        return { ...state, contest_fot_from:'', contest_fot_to:'', date_range:91 } 
      case 'GET_DATE_RANGE':
        return { ...state, date_range: action.data }
      case 'GET_ALL_REPORTS_INIT':
        return { ...state, isloading:true }
      case 'SET_DOWNLOAD_ON':
        return { ...state, download_timer:true }
      case 'SET_DOWNLOAD_OFF':
        return { ...state, download_timer:false }
      case 'GET_FIX_LOADER':
        return { ...state, fixloader:true }
      case 'CONTEST_FROM':
        return { ...state, contest_fot_from: action.data }
      case 'CONTEST_TO':
        return { ...state, contest_fot_to: action.data }  
      case 'WINNER_FROM':
        return { ...state, winner_fot_from: action.data }
      case 'WINNER_TO':
        return { ...state, winner_fot_to: action.data }  
      case 'QUIZ_FROM':
        return { ...state, quiz_fot_from: action.data }
      case 'QUIZ_TO':
        return { ...state, quiz_fot_to: action.data }  
      case 'GET_ALL_CONTEST_REPORTS':
        return { ...state, football_contest: action.data.data, football_contest_perpage: action.data, isloading: false }
      case 'GET_ALL_WINNERS_REPORTS':
        return { ...state, football_winner: action.data.data, football_winner_perpage: action.data, isloading: false }
      case 'GET_ALL_QUIZ_REPORTS':
        return { ...state, football_quiz: action.data.data, football_quiz_perpage: action.data, isloading: false }
      case 'FOOTBALL_SERIES_NAME':
        return { ...state, football_series_name: action.data.data, isloading:false }
      case 'FOOTBALL_FIXTURE_NAME':
        return { ...state, football_fixture_name: action.data, isloading:false, fixloader: false }
      case 'COLLECT_SERIESID':
        return { ...state, football_seriesid: action.data }
      case 'COLLECT_FIXTUREID':
        return { ...state, football_fixtureid: action.data }  
      case 'FOOTBALL_PLAYER_REPORT':
        return { ...state, football_player_reports: action.data }
      case 'GET_VALUE':
        return { ...state, getval: action.data }
      case 'FOOTBALL_SERIES_TOTAL':
        return {...state, football_total: action.data}  
      default:
        return { ...state }
    }
  }

  export default reports