const initialState = {
isloading: false,
cricket_prize_master:[],
football_prize_master:[],
get_cricket_prize:[],
get_football_prize:[],
toggle_data:'',
adData: [],
prizeList: []
}   

const ads = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_add_data:[], football_add_data:[] } 
    case 'GET_ALL_CRICKET_CONTEST_PRIZE':
      return { ...state, isloading: true }
    case 'PRIZE_MASTER_CRICKET':
      return { ...state, get_cricket_prize: action.data, isloading: false }
    case 'PRIZE_MASTER_CRICKET_FOOTBALL':
      return { ...state, get_football_prize: action.data, isloading: false }
    case 'PRIZE_MASTER_CRICKET_TYPE':
      return { ...state, get_cricket_prize_type: action.data, isloading: false }
    case 'ADD_PRIZE_MASTER_CRICKET':
      return { ...state, cricket_prize_master: action.data }
    case 'ADD_PRIZE_MASTER_FOOTBALL':
      return { ...state, football_prize_master: action.data.data }
    case 'GET_ALL_USERS_FAIL':
      return { ...state, isloading: false }  
    case 'SPINNER':
      return { ...state, isloading: true }
    case 'GET_AD_DATA':
      return { ...state, adData: action.data }  
    case 'AD_TOGGLE_STATUS':
      return {...state, toggle_data:action.data, isloading: false}  
    case 'EDIT_AD_DATA':
      return { ...state, editData: action.data, isloading: false }  
    case 'GET_PRIZE_LIST':
      return { ...state, prizeList: action.data }   
    default:
      return { ...state }
  }
}

export default ads
