// ** Initial State
const initialState = {
  isloading: false,
  download_loading: false,
  fixloader: true,
  cricket_contest: [],
  cricket_winner: [],
  cricket_quiz: [],
  cricket_contest_perpage: [],
  cricket_winner_perpage: [],
  cricket_quiz_perpage: [],
  series_name: [],
  fixture_name: [],
  contest_from: '',
  contest_to: '',
  winner_from: '',
  winner_to: '',
  quiz_from: '',
  quiz_to: '',
  seriesid: '',
  fixtureid: '',
  cric_add_data: [],
  getval: '',
  get_fixture:[],
  date_range: 91,
  get_user:{}
}

const rep = (state = initialState, action) => {
  switch (action.type) {
    case 'EMPTY_ID':
      return { seriesid: '', fixtureid: '', getval: '' }
    case 'CLEAR_DATA':
      return { ...state, contest_from: '', contest_to: '', date_range: 91 }
    case 'GET_ALL_REPORTS_INIT':
      return { ...state, isloading: true }
    case 'SET_DOWNLOAD_ON':
      return { ...state, download_loading: true }
    case 'SET_DOWNLOAD_OFF':
      return { ...state, download_loading: false }
    case 'GET_FIX_LOADER':
      return { ...state, fixloader: true }
    case 'GET_DATE_RANGE':
      return { ...state, date_range: action.data }
    case 'CRIC_CONTEST_TO':
      return { ...state, contest_to: action.data }
    case 'CRIC_CONTEST_FROM':
      return { ...state, contest_from: action.data }
    case 'CRIC_WINNER_TO':
      return { ...state, winner_to: action.data }
    case 'CRIC_WINNER_FROM':
      return { ...state, winner_from: action.data }
    case 'CRIC_QUIZ_TO':
      return { ...state, quiz_to: action.data }
    case 'CRIC_QUIZ_FROM':
      return { ...state, quiz_from: action.data }
    case 'GET_ALL_CONTEST_REPORTS':
      return { ...state, cricket_contest: action.data.data, cricket_contest_perpage: action.data, isloading: false }
    case 'GET_ALL_WINNERS_REPORTS':
      return { ...state, cricket_winner: action.data.data, cricket_winner_perpage: action.data, isloading: false }
    case 'GET_ALL_QUIZ_REPORTS':
      return { ...state, cricket_quiz: action.data.data, cricket_quiz_perpage: action.data, isloading: false }
    case 'SERIES_NAME':
      return { ...state, series_name: action.data.data, isloading: false }
    case 'FIXTURE_NAME':
      return { ...state, fixture_name: action.data, isloading: false, fixloader: false }
    case 'PLAYER_REPORT':
      return { ...state, player_reports: action.data }
    case 'GET_VALUE':
      return { ...state, getval: action.data }
    case 'GET_SERIES':
      return { ...state, get_series: action.data }
    case 'GET_FIXTURE':
      return { ...state, get_fixture: action.data }
    case 'REPORT_FIXTURE_NAME':
      return { ...state, report_fixture_name: action.data, isloading:false, fixloader: false }  
    case 'GET_USERTYPE':
      return { ...state, get_user: action.data }
    case 'COLLECT_SERIESID':
      return { ...state, seriesid: action.data }
    case 'COLLECT_FIXTUREID':
      return { ...state, fixtureid: action.data }
    case 'CRICKET_CONTEST_PRIZE_ADD':
      return { ...state, cric_add_data: action.data.data, cricket_prize_page: action.data, isloading: false }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    default:
      return { ...state }
  }
}

export default rep