// ** Initial State
const initialState = {
  isloading: false,
  teams_perpage: [],
  football_teams: [],
  cricket_teams: [],
  cricket_page: [],
  football_page: [],
  cricket_name: [],
  football_name: [],
  cricket_teams_series: [],
  football_teams_series: [],
  cricket_team_view: [],
  football_team_view: [],
  cricket_total: [],
  football_total: [],
  seriescricketid:[]

}

const teams = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_teams_series: [], football_teams_series: [] }
    case 'GET_ALL_TEAMS_INIT':
      return { ...state, isloading: true }
    case 'GET_CRICKET_TEAMS':
      return { ...state, cricket_teams: action.data.data, cricket_page: action.data, isloading: false }
    case 'GET_FOOTBALL_TEAMS':
      return { ...state, football_teams: action.data.data, football_page: action.data, isloading: false }
    case 'SERIES_NAME':
      return { ...state, cir_series_name: action.data.data, cricket_page: action.data, isloading: false }
    case 'CRICKET_TEAM_VIEW':
      return { ...state, cricket_team_view: action.data.data }
    case 'FOOTBALL_TEAM_VIEW':
      return { ...state, football_team_view: action.data.data }
    case 'FOOTBALL_SERIES_NAME':
      return { ...state, football_name: action.data.data, isloading: false }
    case 'GET_CRICKET_TEAMS_SERIES':
      return { ...state, cricket_teams_series: action.data.data, cricket_page: action.data, isloading: false }

    case "CRICKET_SERIES_NAME":
      return { ...state, cricket_name: action.data.data }
    case 'GET_FOOTBALL_TEAMS_SERIES':
      return { ...state, football_teams_series: action.data.data, football_page: action.data,  isloading: false }
    case 'COLLECT_CRIC_SERIESID':
      return { ...state, seriescricketid: action.data }
    case 'COLLECT_FOOT_SERIESID':
      return { ...state, seriesfootballid: action.data }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    case 'FOOTBALL_SERIES_TOTAL':
      return { ...state, football_total: action.data }
    default:
      return { ...state }
  }
}

export default teams
