// ** Initial State
const initialState = {
  isloading: false,
  football_contest: [],
  cricket_contest: [],
  contest_perpage: [],
  cricket_page: [],
  football_page: [],
  cricket_contest_add: [],
  football_contest_add: [],
  searchCricketContest: [],
  searchFootballContest: [],
  cricket_total: [],
  football_total: []
}
const series = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_contest_add: [], football_contest_add: [] }
    case 'GET_ALL_CONTEST_INIT':
      return { ...state, isloading: true }
    case 'GET_CRICKET_CONTEST':
      return { ...state, cricket_contest: action.data.data, cricket_page: action.data, isloading: false }
    case 'GET_FOOTBALL_CONTEST':
      return { ...state, football_contest: action.data.data, football_page: action.data, isloading: false }
    case 'CRICKET_CONTEST_ADD':
      return { ...state, cricket_contest_add: action.data.data, cricket_page: action.data, isloading: false }
    case 'FOOTBALL_CONTEST_ADD':
      return { ...state, football_contest_add: action.data.data, football_page: action.data, isloading: false }
    case 'SEARCH_CRICKET_CONTEST':
      return { ...state, searchCricketContest: action.data }
    case 'SEARCH_FOOTBALL_CONTEST':
      return { ...state, searchFootballContest: action.data }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    case 'FOOTBALL_SERIES_TOTAL':
      return { ...state, football_total: action.data }
    default:
      return { ...state }
  }
}

export default series

