const initialState = {
  cricket_data: [],
  football_data: [],
  cricket_page: [],
  football_page: [],
  isloading: false,
  cricket_add_data: [],
  football_add_data: [],
  cricket_Fixture_add: [],
  cricket_Fixture_edit_view: [],
  cricket_Fixture_edit: [],
  football_Fixture_add: [],
  football_Fixture_view: [],
  football_Fixture_edit: [],
  toggle_status: '',
  football_toggle: '',
  cricketSearchFixture: [],
  footballsearchFixture: [],
  cricket_total: [],
  football_total: [],
  cric_to:'',
  cric_from:'',
  football_from:'',
  football_to:'',
  download_timer: false,
  cricket_teams:[],
  football_teams:[],
  cricket_date_range: 91,
  football_date_range: 91,
  cricket_fixture:[],
  cricket_fixture_page:[],
  cricket_contest:[],
  cricket_contest_page:[]
}

const fix = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_add_data: [], football_add_data: [] }
    case 'GET_ALL_FIXTURE_INIT':
      return { ...state, isloading: true }
    case 'SET_DOWNLOAD_ON':
      return { ...state, download_timer:true }
    case 'SET_DOWNLOAD_OFF':
      return { ...state, download_timer:false }
    case 'GET_CRICKET_DATE_RANGE':
      return { ...state, cricket_date_range: action.data }
    case 'GET_FOOTBALL_DATE_RANGE':
      return { ...state, football_date_range: action.data }
    case 'CRIC_TO':
      return { ...state, cric_to: action.data }
    case 'CRIC_FROM':
      return { ...state, cric_from: action.data }
    case 'FOOTBALL_FROM':
      return { ...state, football_from: action.data }
    case 'FOOTBALL_TO':
      return { ...state, football_to: action.data }
    case 'CRICKET_FIXTURE':
      return { ...state, cricket_data: action.data.data, cricket_page: action.data, isloading:false }
    case 'CRICKET_FIXTURE_SHOW':
      return { ...state, cricket_fixture: action.data.data, cricket_fixture_page: action.data, isloading:false }
    case 'CRICKET_CONTEST_SHOW':
      return { ...state, cricket_contest: action.data, cricket_contest_page: action.data }
    case 'CRICKETFIXTURE_ADD':
      return { ...state, cricket_Fixture_add: action.data.data }
    case 'VIEW_FIXTURE':
      return { ...state, cricket_Fixture_edit_view: action.data.data }
    case 'EDIT_FIXTURES':
      return { ...state, cricket_Fixture_edit: action.data.data }
    case 'CRICKET_FIXTURE_ADD':
      return { ...state, cricket_add_data: action.data.data, cricket_page: action.data, isloading: false }
    case 'FOOTBALL_FIXTURE':
      return { ...state, football_data: action.data.data, football_page: action.data, isloading:false }
    case 'FOOTBALLFIXTURE_ADD':
      return { ...state, football_Fixture_add: action.data.data, isloading:false }
    case 'VIEW_FOOTBALL_FIXTURE':
      return { ...state, football_Fixture_view: action.data.data }
    case 'EDIT_FOOTBALL_FIXTURES':
      return { ...state, football_Fixture_edit: action.data.data }
    case 'FIXTURE_TOGGLE_STATUS':
      return { ...state, toggle_status: action.data }
    case 'FIXTURE_FOOTBALL_TOGGLE_STATUS':
      return { ...state, football_toggle: action.data }
    case 'FOOTBALL_FIXTURE_ADD':
      return { ...state, football_add_data: action.data.data, football_page: action.data, isloading: false }
    case 'SEARCH_CRICKET_FIXTURE':
      return { ...state, cricketSearchFixture: action.data }
    case 'SEARCH_FOOTBALL_FIXTURE':
      return { ...state, footballsearchFixture: action.data }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    case 'GET_CRICKET_TEAMS':
      return {...state, cricket_teams:action.data}  
    case 'GET_FOOTBALL_TEAMS':
      return {...state, football_teams:action.data}  
    case 'FOOTBALL_SERIES_TOTAL':
      return { ...state, football_total: action.data }
    default:
      return { ...state }
  }
}
export default fix
