const initialState = {
  allContests: [],
  data: [],
  addprize:[],
  editcontestsprize:{},
  editPostData: {},
  editPostSuccess: [],
  editPostLoader: false
}

const footballcontestprize = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_ALL_CONTESTS':
      return { ...state, allContests: action.data.records }
    case 'DELETE_PRIZE':
      return { ...state }
    case 'ADD_PRIZE':
      return { ...state, addprize: action.data.data }
      case 'EDIT_PRIZE_CONTESTS':
        return { ...state, editcontestsprize: action.data.records }
    case 'GET_FOOTBALL_CONTESTS_PRIZE':
      return { ...state, data: action.data.records }
    
    default:
      return { ...state }
  }
}

export default footballcontestprize
