const initialState = {
  cricket_data: [],
  football_data: [],
  isloading: false,
  cricket_page: [],
  football_page: [],
  cricket_data_add: [],
  football_data_add: [],
  football_squad_add: [],
  cricket_add_squad: [],
  cricket_view_squad: [],
  searchCricketSquad: [],
  searchFootballSquad: [],
  cricket_total: [],
  football_total: []
}

const squad = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, cricket_data_add: [], football_data_add: [] }
    case 'GET_ALL_SQUAD_INIT':
      return { ...state, isloading: true }
    case 'CRICKET_SQUAD':
      return { ...state, cricket_data: action.data.data, cricket_page: action.data, isloading: false }
    case 'CRICKET_SQUAD_ADD':
      return { ...state, cricket_data_add: action.data.data, cricket_page: action.data, isloading: false }
    case 'CRICKET_ADD_SQUAD':
      return { ...state, cricket_add_squad: action.data.data }
    case 'CRICKET_SQUAD_VIEW':
      return { ...state, cricket_view_squad: action.data.data }
    case 'FOOTBALL_SQUAD_ADD':
      return { ...state, football_data_add: action.data.data, football_page: action.data, isloading: false }
    case 'FOOTBALL_SQUAD_VIEW':
      return { ...state, football_view_squad: action.data.data }
    case 'FOOTBALLSQUAD_ADD':
      return { ...state, football_squad_add: action.data.data }
    case 'FOOTBALL_SQUAD':
      return { ...state, football_data: action.data.data, football_page: action.data, isloading: false }
    case 'SEARCH_CRICKET_SQUAD':
      return { ...state, searchCricketSquad: action.data }
    case 'SEARCH_FOOTBALL_SQUAD':
      return { ...state, searchFootballSquad: action.data }
    case 'CRICKET_SERIES_TOTAL':
      return { ...state, cricket_total: action.data }
    case 'FOOTBALL_SERIES_TOTAL':
      return { ...state, football_total: action.data }
    default:
      return { ...state }
  }
}

export default squad
