// ** Initial State
const initialState = {
  dataPerPage: '',
  isloading: false,
  football_series: [],
  cricket_series: [],
  series_perpage: [],
  football_page: [],
  cricket_page: [],
  view_cricket_series: [],
  view_football_series: [],
  searchCricketUsers: []
}

const series = (state = initialState, action) => {
  switch (action.type) {
    case 'CLEAR_DATA':
      return { ...state, dataPerPage: '' }
    case 'GET_DATA_PERPAGE':
      return { ...state, dataPerPage: action.data }
    case 'GET_ALL_SERIES_INIT':
      return { ...state, isloading: true }
    case 'GET_CRICKET_SERIES':
      return { ...state, cricket_series: action.data.data, cricket_page: action.data, isloading: false }
    case 'VIEW_CRICKET_SERIES':
      return { ...state, view_cricket_series: action.data.data }
    case 'GET_FOOTBALL_SERIES':
      return { ...state, football_series: action.data.data, football_page: action.data, isloading: false }
    case 'VIEW_FOOTBALL_SERIES':
      return { ...state, view_football_series: action.data.data }
    case 'SEARCH_CRICKET_USERS':
      return { ...state, searchCricketUsers: action.data, cricket_series: [] }
    case 'SEARCH_FOOTBALL_USERS':
      return { ...state, searchFootballUsers: action.data, football_series: [] }
    default:
      return { ...state }
  }
}

export default series
