// ** Initial State
const initialState = {
  feedAllData: [],
  addSuccessData: '',
  addPostLoader: false,
  loadFeedAll: false,
  editPostData: {},
  editPostSuccess: '',
  editPostLoader: false,
  delete: false,
  view:{},
  wall_page:[],
  user_feed:[]
}

const feeds = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_ALL_WALL_DATA_INIT':
      return { ...state, loadFeedAll: true }
    case 'WALL_DELETE':
      return {delete: true }  
    case 'GET_WALL_VIEW':
      return { ...state, view: action.data.data}
    case 'GET_ALL_WALL_POST': 
      return { ...state, feedAllData: action.data.data, wall_page: action.data, loadFeedAll: false }
    case 'GET_ALL_WALL_DATA_FAIL':
      return { ...state, loadFeedAll: false }
    case 'ADD_FEED_POST':
      return { ...state, addSuccessData: action.data, addPostLoader: false }
    case 'GET_ALL_WALL_COMMENNT':
      return { ...state, user_feed: action.data, addPostLoader: false }
    case 'ADD_FEED_POST_INIT': 
      return { ...state, addPostLoader: true}
    case 'ADD_FEED_POST_FAIL': 
      return { ...state, addPostLoader: false}
    case 'RESET_ADD_POST':
      return { ...state,  addSuccessData: '' }
    case 'EDIT_POST_DATA':
      return { ...state, editPostData: action.data, editPostLoader: false }
    case 'EDIT_POST_INIT':
      return { ...state, editPostLoader: true}
    case 'EDIT_FEED_POST':
      return { ...state, editPostSuccess: action.data, editPostLoader: false }
    case 'RESET_EDIT_POST':
     return { ...state,  editPostData: '' }
    case 'EDIT_POST_FAIL':
      return { ...state, editPostLoader: false }
    default:
      return { ...state }
  }
}
export default feeds
