// ** Initial State
const initialState = {
  feedAllData: [],
  addSuccessData: '',
  addPostLoader: false,
  loadFeedAll: false,
  editPostSuccess: {},
  editPostLoader: false,
  delete: false,
  view:{},
  wall_page:[],
  isloading:false,
  cricket_name:[],
  football_name:[],
  banner_list:[],
  football_banner:[],
  banner_add:[],
  banner_status:'',
  editPostData: {},
  viewbanners: [],
  editstatus: [],
  cricket_total:[],
  football_total:[]
 
}

const banner = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_ALL_WALL_DATA_INIT':
      return { ...state, loadFeedAll: true }
    case 'WALL_DELETE':
      return {delete: true }  
    case 'GET_WALL_VIEW':
      return { ...state, view: action.data.data}
    case 'GET_ALL_WALL_POST': 
      return { ...state, feedAllData: action.data.data, wall_page: action.data, loadFeedAll: false }
    case 'GET_ALL_WALL_DATA_FAIL':
      return { ...state, loadFeedAll: false }
    case 'ADD_FEED_POST':
      return { ...state, addSuccessData: action.data, addPostLoader: false }
    case 'ADD_FEED_POST_INIT': 
      return { ...state, addPostLoader: true}
    case 'ADD_FEED_POST_FAIL': 
      return { ...state, addPostLoader: false}
    case 'RESET_ADD_POST':
      return { ...state,  addSuccessData: '' }

    case 'EDIT_POST_INIT':
      return { ...state, editPostLoader: true}
    case 'EDIT_FEED_POST':
      return { ...state, editPostSuccess: action.data, editPostLoader: false }
    case 'EDIT_POST_FAIL':
      return { ...state, editPostLoader: false }

   //cricket series
    case 'CRICKET_SERIES_NAME':
      return { ...state, cricket_name: action.data.data, isloading: false } 
   //football series
    case 'FOOTBAL_SERIES_NAME':
      return {...state, football_name: action.data.data}   
  //cricket seriesid
   case 'GET_ALL_BANNER_DATA_INIT':
      return {...state, isloading: true}     
   case 'COLLECT_CRIC_SERIESID':
      return { ...state, seriescricketid: action.data, isloading: false}  
   case 'GET_ALL_BANNER_TYPE':
      return {...state, banner_list: action.data, editstatus: [], isloading: false} 
   case 'GET_FOOTBALL_BANNER_LIST':
      return {...state, football_banner: action.data, isloading: false}    
   case 'BANNER_ADD':
      return {...state, banner_add: action.data.data}   
    case 'BANNER_TOGGLE_STATUS':
      return {...state, banner_status: action.data, isloading:false}  
    case 'EDIT_POST_DATA':
      return { ...state, editPostData: action.data }
    case 'GET_BANNER_VIEW':
      return {...state, viewbanners:action.data}  
    case 'EDIT_BANNER_POST':
      return {...state, editstatus:action.data}  
    case 'CRICKET_SERIES_TOTAL':
      return {...state, cricket_total:action.data}  
    case 'FOOTBALL_SERIES_TOTAL':
      return {...state, football_total:action.data}  
     default:
      return { ...state }
  }
}
export default banner
