const initialState = {
  userSignupCalendarCount: {},
  userSignUpCountPlatform: {},
  liveMatchesLists: {},
  contestsLists: {},
  picker:[]
}

const dashboard = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_USER_COUNT_CALENDAR':
      return { ...state, userSignupCalendarCount: action.data }
    case 'GET_USER_COUNT_PLATFORM':
      return { ...state, userSignUpCountPlatform: action.data }
    case 'GET_LIVE_MATCHES_LIST':
      return { ...state, liveMatchesLists: action.data }
    case 'GET_CONTESTS_LIST':
      return { ...state, contestsLists: action.data }
    case 'PICK_DATE':
      return { ...state, picker: action.data }
    default:
      return { ...state }
  }
}

export default dashboard
