// ** Initial State
const initialState = {
    isloading: false,
    preview_data:[],
    football_preview_data:[],
    add_preview:[],
    add_foot_que_preview:[],
    predictor_name:[],
    predict_football_name:[]
  }
  
  const predictor = (state = initialState, action) => {
      switch (action.type) {
        case 'GET_ALL_REPORTS_INIT':
          return { ...state, isloading:true }
        case 'CHECK_PREVIEW_DATA':
          return {...state, preview_data: action.data}  
        case 'FOOTBALL_PREVIEW_DATA':
          return {...state, football_preview_data:action.data} 
         case 'GET_ALL_PREDICTOR':
          return {...state, predictor_name:action.data, isloading:false} 
        case 'GET_FOOTBALL_PREDICTOR':
          return {...state, predict_football_name:action.data, isloading:false}  
        case 'Add_PREVIEW_DATA':
          return {...state, add_preview: action.data} 
        case 'Add_FOOT_QUE_PREVIEW':
          return {...state, add_foot_que_preview: action.data}    
       default:
          return { ...state }
      }
    }
  
    export default predictor
  