const initialState = {
  isloading: false,
  isloader: false,
  cricket_prize_master:[],
  football_prize_master:[],
  get_cricket_prize:[],
  get_football_prize:[],
  redeem_list: [],
  redeem_page: []
  }
  
  const redeem = (state = initialState, action) => {
    switch (action.type) {
      case 'CLEAR_DATA':
        return { ...state, cricket_add_data:[], football_add_data:[] } 
      case 'REDEEM_LIST':
        return { ...state, redeem_list: action.data.data, redeem_page: action.data, isloader: false }
      case 'GET_ALL_CRICKET_CONTEST_PRIZE':
        return { ...state, isloading: true }
      case 'GET_SPINNER':
        return { ...state, isloader: true }
      case 'PRIZE_MASTER_CRICKET':
        return { ...state, get_cricket_prize: action.data, isloader: false }
      case 'PRIZE_MASTER_CRICKET_FOOTBALL':
        return { ...state, get_football_prize: action.data, isloader: false }
      case 'PRIZE_MASTER_CRICKET_TYPE':
        return { ...state, get_cricket_prize_type: action.data, isloading: false }
      case 'ADD_PRIZE_MASTER_CRICKET':
        return { ...state, cricket_prize_master: action.data }
      case 'ADD_PRIZE_MASTER_FOOTBALL':
        return { ...state, football_prize_master: action.data.data }
      case 'GET_ALL_USERS_FAIL':
        return { ...state, isloading: false }  
      default:
        return { ...state }
    }
  }
  
  export default redeem
  