// ** Initial State
const initialState = {
  isloading: false,
  download_time: false,
  referral_report: [],
  user_report : [],
  redeem_eport : [],
  user_from:"",
  user_to:"",
  reddeem_from:'',
  reddeem_to:'',
  referral_from:'',
  referral_to:'',
  referral_page: [],
  user_page: [],
  redeem_page: [],
  date_range:91
}

const basereport = (state = initialState, action) => {
    switch (action.type) {
      case 'CLEAR_DATA':
        return { ...state, referral_from:'', referral_to:'', date_range:91 } 
      case 'GET_DATE_RANGE':
        return { ...state, date_range: action.data }
      case 'GET_ALL_REPORTS_INIT':
        return { ...state, isloading:true }
      case 'SET_DOWNLOAD_ON':
        return { ...state, download_time:true }
      case 'SET_DOWNLOAD_OFF':
        return { ...state, download_time:false }
      case 'USER_FROM':
        return { ...state, user_from: action.data }
      case 'USER_TO':
        return { ...state, user_to: action.data }
      case 'REDEEM_FROM':
        return { ...state, reddeem_from: action.data }
      case 'REDEEM_TO':
        return { ...state, reddeem_to: action.data }
      case 'REFERRAL_FROM':
        return { ...state, referral_from: action.data }
      case 'REFERRAL_TO':
        return { ...state, referral_to: action.data }
      case 'GET_ALL_REFERRAL_REPORTS':
        return { ...state, referral_report: action.data.data, referral_page: action.data, isloading: false }
      case 'GET_ALL_USER_REPORTS':
        return { ...state, user_report: action.data.data, user_page: action.data, isloading: false }
      case 'GET_ALL_REDEEM_REPORTS':
        return { ...state, redeem_eport: action.data.data, redeem_page: action.data, isloading: false }
      default:
        return { ...state }
    }
}

  export default basereport