// ** Initial State
const initialState = {
  isloading: false,
  allTeams: [],
  cricket_contest: [],
  cricket_winner : [],
  cricket_quiz : [],
  cricket_contest_perpage: [],
  cricket_winner_perpage: [],
  cricket_quiz_perpage: [],
  series_name:[],
  fixture_name:[],
  contest_from:'',
  contest_to:'',
  winner_from:'',
  winner_to:'',
  quiz_from:'',
  quiz_to:'',
  seriesid:'',
  fixtureid:'',
  cric_add_data:[]
}

const corporatefixture = (state = initialState, action) => {
    switch (action.type) {
      case 'GET_CRICKET_TEAMS':
        return { ...state, allTeams: action.data }
      case 'GET_FOOTBALL_TEAMS':
        return { ...state, allTeams: action.data }  
      case 'GET_ALL_REPORTS_INIT':
        return { ...state, isloading:true }
      case 'CRIC_CONTEST_TO':
        return { ...state, contest_to: action.data }
      case 'CRIC_CONTEST_FROM':
        return { ...state, contest_from: action.data }
      case 'CRIC_WINNER_TO':
        return { ...state, winner_to: action.data }
      case 'CRIC_WINNER_FROM':
        return { ...state, winner_from: action.data }
      case 'CRIC_QUIZ_TO':
        return { ...state, quiz_to: action.data }
      case 'CRIC_QUIZ_FROM':
        return { ...state, quiz_from: action.data }
      case 'GET_ALL_CONTEST_REPORTS':
        return { ...state, cricket_contest: action.data.data, cricket_contest_perpage: action.data, isloading: false }
      case 'GET_ALL_WINNERS_REPORTS':
        return { ...state, cricket_winner: action.data.data, cricket_winner_perpage: action.data, isloading: false }
      case 'GET_ALL_QUIZ_REPORTS':
        return { ...state, cricket_quiz: action.data.data, cricket_quiz_perpage: action.data, isloading: false }
      case 'SERIES_NAME':
        return { ...state, series_name: action.data.data, isloading:false }
      case 'FIXTURE_NAME':
        return { ...state, fixture_name: action.data, isloading:false }
      case 'PLAYER_REPORT':
        return { ...state, player_reports: action.data }
      case 'COLLECT_SERIESID':
        return { ...state, seriesid: action.data }
      case 'COLLECT_FIXTUREID':
        return { ...state, fixtureid: action.data } 
        case 'CRICKET_CONTEST_PRIZE_ADD':
          return { ...state, cric_add_data: action.data.data, cricket_prize_page:action.data,  isloading: false }
      default:
        return { ...state }
    }
  }

  export default corporatefixture
